<template>
  <v-card>
    <div
      v-bind="localAttrs"
      :padless="padless"
      style="margin-top: 2%;"
    >
      <v-card
        flat
        tile
        width="100%"
        class="red lighten-1 text-center"
      >
        <v-card-text>
          <v-btn
            v-for="icon in icons"
            :key="icon"
            class="mx-4"
            icon
          >
            <v-icon color="#E8917B" size="37px">
              {{ icon }}
            </v-icon>
          </v-btn>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-text style="color:#E8917B; font-size: 17px;">
          &copy;{{ new Date().getFullYear() }} — <strong>Pain de Vie</strong>
        </v-card-text>
      </v-card>
    </div>
  </v-card>
</template>

<script>
export default {
    data: () => ({
      icons: [
        'mdi-email',
        'mdi-youtube',
        'mdi-instagram',
      ],
      items: [
        'default',
        'absolute',
        'fixed',
      ],
      padless: false,
      variant: 'default',
    }),
    computed: {
      localAttrs () {
        const attrs = {}

        if (this.variant === 'default') {
          attrs.absolute = false
          attrs.fixed = false
        } else {
          attrs[this.variant] = true
        }
        return attrs
      },
    },

}
</script>

<style lang="scss" scoped>
  div {
    background-color: #EDEDED;
  }

</style>