import Vue from 'vue'
import VueRouter from 'vue-router'
import VueMeta from 'vue-meta'
import HomeView from '../views/HomeView.vue'
import FirstVisite from '../views/FirstVisite.vue'
import Community from '../views/Community.vue'
import Activity from '../views/Activity.vue'
import Contact from '../views/Contact.vue'
import Sustain from '../views/Sustain.vue'
import NotFound from '../views/NotFound.vue'
import Ressource from '../views/Ressource.vue'

Vue.use(VueRouter)
Vue.use(VueMeta)

// route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/visite',
    name: 'visite',
    component: FirstVisite,
    meta: {
      title: 'Visite - Eglise Pain de Vie',
      metaTags: [
        {
          name: 'description',
          content: 'Page pour les premières visites.'
        },
        {
          property: 'og:description',
          content: 'Page pour les premières visites.'
        }
      ]
    }
  },
  {
    path: '/communaute',
    name: 'community',
    component: Community,
    meta: {
      title: 'Communauté - Eglise Pain de Vie',
      metaTags: [
        {
          name: 'description',
          content: 'Page savoir plus sur nos communautés'
        },
        {
          property: 'og:description',
          content: 'Page savoir plus sur nos communautés'
        }
      ]
    }
  },
  {
    path: '/activity',
    name: 'activity',
    component: Activity,
    meta: {
      title: 'Communauté - Eglise Pain de Vie',
      metaTags: [
        {
          name: 'description',
          content: 'Page savoir plus sur nos communautés'
        },
        {
          property: 'og:description',
          content: 'Page savoir plus sur nos communautés'
        }
      ]
    }
  },
  {
    path: '/Ressources',
    name: 'ressource',
    component: Ressource,
    meta: {
      title: 'Ressources - Eglise Pain de Vie',
      metaTags: [
        {
          name: 'description',
          content: 'Page nos ressources'
        },
        {
          property: 'og:description',
          content: 'Page nos ressources'
        }
      ]
    }
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact,
    meta: {
      title: 'Contact - Eglise Pain de Vie',
      metaTags: [
        {
          name: 'description',
          content: 'Page nous contacter'
        },
        {
          property: 'og:description',
          content: 'Page nous contacter'
        }
      ]
    }
  },
  {
    path: '/nous-soutenir',
    name: 'sustain',
    component: Sustain,
    meta: {
      title: 'Don - Eglise Pain de Vie',
      metaTags: [
        {
          name: 'description',
          content: 'Page soutenir l\'oeuvre du Seigneur'
        },
        {
          property: 'og:description',
          content: 'Page soutenir l\'oeuvre du Seigneur'
        }
      ]
    }
  },

  //Catchall 404
  {
    path: '/:catchAll(.*)',
    name: 'NotfFound',
    component: NotFound
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
